import { createGlobalStyle } from 'styled-components';

import LatoLight from './fonts/Lato/Lato-Light.ttf';
import LatoRegular from './fonts/Lato/Lato-Regular.ttf';
import LatoBold from './fonts/Lato/Lato-Bold.ttf';

export default createGlobalStyle`

    @font-face {
        font-family: 'Lato-Light';
        src: url(${LatoLight}) format('truetype');
    }
    @font-face {
        font-family: 'Lato-Bold';
        src: url(${LatoBold}) format('truetype');
    }
    @font-face {
        font-family: 'Lato-Regular';
        src: url(${LatoRegular}) format('truetype');
    }

    /*Zeroing*/
    * {
        padding: 0;
        margin: 0;
        border: 0;
    }
    *,
    *:before,
    *:after {
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
    }
    :focus,
    :active {
        outline: none;
    }
    a:focus,
    a:active {
        outline: none;
    }
    nav,
    footer,
    header,
    aside {
        display: block;
    }
    html,
    body {
        height: 100%;
        width: 100%;
        font-size: 100%;
        line-height: 1.5;
        -ms-text-size-adjust: 100%;
        -moz-text-size-adjust: 100%;
        -webkit-text-size-adjust: 100%;
        transition: ease .3s background-color, color;
        color: ${(props: any) => props.theme.colors.textColor};
        font-size: ${(props: any) => props.theme.fontSizes.medium};
        font-family: ${(props: any) => props.theme.fonts[0]};
        background-color: ${(props: any) => props.theme.colors.backgroundColor};
    }
    input,
    button,
    textarea,
    select {
        font-family: inherit;
    }
    input::-ms-clear {
        display: none;
    }
    button {
        cursor: pointer;
    }
    button::-moz-focus-inner {
        padding: 0;
        border: 0;
    }
    a,
    a:visited {
        text-decoration: none;
        color: ${(props: any) => props.theme.colors.textColor};

    }
    a:hover {
        text-decoration: none;
    }
    ul li {
        list-style: none;
    }
    img {
        max-width: 100%;
        display: block;
    }
    h1,h2,h3,h4,h5,h6 {
        font-size: inherit;
        font-weight: 400;
    }
`;